import React from 'react';
import ReactPlayer from 'react-player';
import 'aframe';

// Import the Artyom library
import Artyom from 'artyom.js';

// Import the previously created class to handle the commands from another file
import ArtyomCommandsManager from './ArtyomCommands';

const Jarvis = new Artyom();

export default class Conversation extends React.Component {
  constructor (props, context){
      super(props, context);

      // Add `this` context to the handler functions
      this.startAssistant = this.startAssistant.bind(this);
      this.stopAssistant = this.stopAssistant.bind(this);
      this.speakText = this.speakText.bind(this);
      this.handleTextareaChange = this.handleTextareaChange.bind(this);
      this.buttonStart = this.buttonStart.bind(this);
      this.playAudio = this.playAudio.bind(this);
      this.pauseAssistant = this.pauseAssistant.bind(this);
      this.playAssistant = this.playAssistant.bind(this);

      // Prepare simple state
      this.state = {
          artyomActive: false,
          textareaValue: "",
          artyomIsReading: false,
          textState: "Presiona sobre el botón iniciar nuestra conversación", 
          begin: true,
          isplayingVideo: false,
          videopath: require("../videos/Bienvenida.mp4")
      };

      // Load some commands to Artyom using the commands manager
      
  }

  componentDidMount(){
    let CommandsManager = new ArtyomCommandsManager(Jarvis, this.playAudio, this.stopAssistant);
    CommandsManager.loadCommands();

    document.getElementById("videoPanel").addEventListener("ended", (e) => {
        //change the sphere material + play the new video
        console.log("Video finalizadoooo");
        this.playAssistant();
    });

    document.getElementById("videoPanel").addEventListener("playing", (e) => {
        //change the sphere material + play the new video
        console.log("Video iniciado");
        this.pauseAssistant();
    });

  }

  buttonStart(){
    let _this = this;
      if(this.state.artyomActive){
          this.stopAssistant();
      }else{
          //this.startAssistant();
          this.startAssistant();
          document.getElementById("videoPanel").play();
      }
  }

  startAssistant() {
      let _this = this;
      _this.setState({
        artyomActive: true,
        textState: "...",
        begin :false,
        isplayingVideo:true
    });
  }

  stopAssistant() {
      let _this = this;

      Jarvis.fatality().then(() => {
          console.log("Jarvis has been succesfully stopped");
          //Jarvis.say("Hasta Luego, ¡Regresa pronto!");
          _this.setState({
              artyomActive: false,
              textState: "Presiona sobre el botón para iniciar nuestra conversación",
              begin: true,
          });
          
      }).catch((err) => {
          console.error("Oops, ha ocurrido un error!", err);

          _this.setState({
              artyomActive: false
          });
      });
  }

  pauseAssistant(){
    let _this = this;

    Jarvis.fatality().then(() => {
 
        
    }).catch((err) => {
        console.error("Oops, ha ocurrido un error!", err);
    });
  }

  playAssistant(){
    let _this = this;
  
    Jarvis.initialize({
        lang: "es-ES",
        debug: true,
        continuous: true,
        soundex: true,
        listen: true,
        mode: "quick",
        //soundex: false, //para que reconozca palabras similares
    }).then(() => {
        //Jarvis.say("Hola, bienvenido al confesionario de la alcaldía de barrancabermeja. Dime como te sientes: ¿triste o feliz?");
        _this.setState({
            isplayingVideo:false,
            textState:"Te escucho"
        });
    }).catch((err) => {
        console.error("Oops, ha ocurrido un error!", err);
    });
  }

  speakText() {
      let _this = this;

      _this.setState({
          artyomIsReading: true
      });

      // Speak text with Artyom
      Jarvis.say( _this.state.textareaValue, {
          onEnd() {
              _this.setState({
                  artyomIsReading: false
              });
          }
      });
  }


playAudio(path) {
    let _this = this;
      _this.setState({
            artyomActive: true,
          isplayingVideo: true,
          videopath: path,
          textState: "...",
          begin :false,
      });

      document.getElementById("videoPanel").play();
      /*if(value){
          this.stopAssistant();
      }*/
}


  handleTextareaChange(event) {
      this.setState({
          textareaValue: event.target.value
      });
  }

  render() {
    //let video = require(this.state.videopath);
    let divGrabando;
    if(this.state.artyomActive){
        if(this.state.isplayingVideo){
            divGrabando = <div className="containerAnimated" >
                <ReactPlayer url={this.state.videopath} 
                controls 
                playing
                onStart = {()=> this.playAssistant()}
                onEnded = {() => this.playAssistant()}
                /></div>
            divGrabando = <a-video src="#videoPanel" 
                width="3.2" 
                height="1.8" 
                position="1.252 2.402 -3.1"
                rotation="0 -21 180">
            </a-video>
        }else{
            divGrabando = 
            <div className="containerAnimated" >
                <div className="circle" style={{animationDelay: "0s"}}></div>
                <div className="circle" style={{animationDelay: "1s"}}></div>
                <div className="circle" style={{animationDelay: "2s"}}></div>
                <div className="circle" style={{animationDelay: "3s"}}></div>
                
            </div>

            divGrabando = <a-image 
                src="#Micro" 
                position="1.252 2.5 -3.1"
                rotation="0 -6.761 0"
                width="1"
                height="1">
            </a-image>
        }
    }
    else {
        divGrabando = 
        <div className="containerAnimated" >
            <img src={require("../images/image001.png")} style={{width: "100%"}} />
        </div>
        divGrabando = ""
    }

    return (
        <div >
            <a-scene >
                <a-entity 
                    light="color: #ffffff; type: ambient; intensity: 2" 
                    data-aframe-default-light="" 
                    aframe-injected="" 
                    position="">
                </a-entity>

                
                <a-assets>
                    <img id="Logo-alcaldia" src={require("../images/image001.png")}/>
                    <img id="Micro" src={require("../images/micro.png")}/>
                    <a-asset-item id="cityModel" src={require("./models/office_computer/scene.glb") }></a-asset-item>
                    <video id="videoPanel" 
                        //preload="auto" 
                         
                        src={this.state.videopath}>
                    </video>
                </a-assets>
                
                <a-entity 
                    geometry="primitive: plane; width: 4; height: 4" 
                    position="0 .772 -3" 
                    rotation="-90 0 0" 
                    material="color: #7BC8A4" />

                <a-image 
                    src="#Logo-alcaldia" 
                    position="-2.181 1.831 -3.871" 
                    rotation="0 30 0"
                    width="2.5"
                    height="1">
                </a-image>
                {divGrabando}
                {/*<a-video src="#videoPanel" 
                    width="3.2" 
                    height="1.8" 
                    position="0.436 1.796 -3.1"
                    rotation="0 -21.686 180.000">
    </a-video>*/}

                <a-entity video-controls="src:#videoPanel"></a-entity>

                <a-gltf-model 
                    src="#cityModel" 
                    position="0.363 0.9 -1.817"
                    scale="0.01 0.01 0.01" 
                    rotation="0 63.306 0" >
                </a-gltf-model>
               

            </a-scene>

            <div className="containerHTML">
                <h1 style={{padding:"0 50px"}}>Bienvenido al Confesionario Virtual</h1>
                
                <div style={{width:"100%", zIndex: 10 }}>
                    <input style={{backgroundColor: this.state.artyomActive? "#f44336":"#008CBA" , zIndex: 1}} type="button" className="button" value={this.state.artyomActive?"Terminar":"Inicia a hablar"} disabled={false} onClick={this.buttonStart}/>

    {/* Speech synthesis Area */}
                    <p>{this.state.textState}</p>
                   
                </div>
                                
            </div>
                            
        </div>
    )
  }
}