import React from "react";
import $ from 'jquery';
import { withRouter } from "react-router-dom";
import Scene from './Scene';

import Artyom from 'artyom.js';
const Jarvis = new Artyom();

class SpeakSelection extends React.Component {
    //let Emotion = "";
    //const history = useHistory(); 


    /*function handleSubmit(evt) {
        evt.preventDefault();
        var radioValue = $("input[name='optradio']:checked").val();
        localStorage.setItem("emotion", radioValue);
        console.log(radioValue);
        history.push("/conversacion");
    }*/

    constructor (props, context){
        super(props, context);
  
        // Add `this` context to the handler functions
        this.StartAssistant = this.StartAssistant.bind(this);
        this.StopAssistant = this.StopAssistant.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleChangeInput = this.handleChangeInput.bind(this);
  
        // Prepare simple state
        this.state = {
            Emotion: "",
            isDictating:false,
            buttonClass: "btn btn-danger"
        };
  
        // Load some commands to Artyom using the commands manager
        
    }

    componentDidMount(){
        Jarvis.addCommands([
            {
                indexes: ["salud", "empleo"],
                action: function(i) {
                    if(i==0){
                        document.getElementById("salud").checked = true;  
                    }

                    if(i==1){
                        document.getElementById("empleo").checked = true;  
                    }
                }
            }

        ]);
    }

    //let User = "";
    //const history = useHistory(); 

    handleSubmit(evt) {
        evt.preventDefault();
        this.StopAssistant();
        var radioValue = $("input[name='optradio']:checked").val();
        localStorage.setItem("theme", radioValue);
        console.log(radioValue);
        this.props.history.push('/conversacion');
    }

    handleChangeInput(e) {
        const inputValue = e.target.value;
        this.setState({Emotion:inputValue})
        console.log(this.state.Emotion)
        //User = inputValue;
    }


    StopAssistant(){
        let _this = this;
        Jarvis.fatality().then(() => { 
            _this.setState({
                isDictating:false,
                buttonClass:"btn btn-danger"
            });   
        }).catch((err) => {
            console.error("Oops, ha ocurrido un error!", err);
        });    
    }
    
    StartAssistant(){

        if(this.state.isDictating){
            this.StopAssistant();
        }else{
            let _this = this;
    
            Jarvis.initialize({
                lang: "es-ES",
                debug: true,
                continuous: true,
                soundex: true,
                listen: true,
                mode: "quick",
                //soundex: false, //para que reconozca palabras similares
            }).then(() => {
                //Jarvis.say("Hola, bienvenido al confesionario de la alcaldía de barrancabermeja. Dime como te sientes: ¿triste o feliz?");
                _this.setState({
                    isDictating:true,
                    buttonClass: "btn btn-warning"
                });
            }).catch((err) => {
                console.error("Oops, ha ocurrido un error!", err);
            });
        }
    }


    render(){
        let micro = ""
        if(!this.state.isDictating){
            micro = <img src={require("../images/micro2.webp")} height="20" alt="USA flag"/>
        }else{
            micro = <img src={require("../images/microred.png")} height="20" alt="USA flag"/>
        }
        return (
            <div>
                <div className="container vh-100" style={{display:"flex", flexDirection:"column", justifyContent:"space-around", zIndex:10}}>
                    <h1 style={{fontSize:"4vw"}} className="font-weight-bold"> ¿De cuál de estos temas te gustaría que conversáramos?</h1>
                
                    <form action="/instrucciones" onSubmit={this.handleSubmit} style={{zIndex:100 }}>
                        
                        
                        <div className="row align-items-center justify-content-center">
                            <div className="form-group">
                                <div className="custom-control custom-radio image-checkbox col-md-4">
                                    <input type="radio" className="custom-control-input" id="salud" value="salud" name="optradio" required/>
                                    <label className="custom-control-label" htmlFor="salud" style={{width:"5em"}}>
                                        <img src= {require("../images/emoticons/salud.png")} alt="#" className="img-fluid"/>
                                        <p className="font-weight-bold font-italic badge bg-success text-white">Salud</p>
                                    </label>
                                </div>
                            </div>

                            <div className="form-group">
                                <div className="custom-control custom-radio image-checkbox col-md-4">
                                    <input type="radio" className="custom-control-input" id="empleo" value="empleo" name="optradio" required/>
                                    <label className="custom-control-label" htmlFor="empleo" style={{width:"5em"}}>
                                        <img src= {require("../images/emoticons/empleo.png")} alt="#" className="img-fluid"/>
                                        <p className="font-weight-bold font-italic badge bg-success text-white">Empleo</p>
                                    </label>
                                </div>
                            </div>
                        </div>

                        <div style={{zIndex:10}}>
                                
                                <div className="form-group jd-flex flex-column ">
            
                                    <div className="row jd-flex justify-content-center">                          
                                        <div className="input-group col-sm-6 text-align:center flex-column">
                                                <span className="input-group-btn">
                                                    <label htmlFor="usuario" className="font-weight-bold bg-primary badge">Puedes presionar sobre el botón rojo y pronunciar el tema del que quieres hablar</label>
                                                </span> 
                                                
                                                <span className="input-group-btn">
                                                    <button className={this.state.buttonClass} type="button" onClick={this.StartAssistant}>
                                                        {micro}
                                                    </button>
                                                </span>
                                                
                                                <button type="submit" className="btn btn-primary mt-3">  Guardar y Continuar  </button>

                                        </div>          
                                    </div>
                                                
                                </div>

                        </div>
                        
                    </form>         
                </div>  
                <Scene></Scene>
            </div>
            
    );
    }
    
};

export default withRouter(SpeakSelection)