import playAudio from '../App'

export default class ArtyomCommandsManager {

    // The ArtyomCommandsManager class expects as argument in the constructor
    // an already declared instance of Artyom.js
    constructor (ArtyomInstance, playAudio, stopAssistant){
        this._artyom = ArtyomInstance;
        this._playAudio = playAudio;
        this._stopAssistant = stopAssistant;

        this.playArtyom = this.playArtyom.bind(this);
        this.stopArtyom = this.stopArtyom.bind(this);
    }
    

    playArtyom(){
        this._playAudio(false, "");
        this._artyom.initialize({
            lang: "es-ES",
            debug: true,
            continuous: true,
            soundex: true,
            listen: true,
            mode: "quick",
            //soundex: false, //para que reconozca palabras similares
        }).then(() => {
            // Display loaded commands in the console
            console.log(this._artyom.getAvailableCommands());
        }).catch((err) => {
            console.error("Oops, ha ocurrido un error!", err);
        });
    }

    stopArtyom(){
        this._stopAssistant();
    }

    // Execute the loadCommands method to inject the methods to the instance of Artyom
    loadCommands(){
        let Artyom = this._artyom;
        let _this = this;
        // Here you can load all the commands that you want to Artyom
        return Artyom.addCommands([
            {
                indexes: ["Hola", "buenos días", "Buenas tardes"],
                action: () => {
                    this._playAudio(require("../videos/Bienvenida.mp4"));                   
                }
            },
            {
                indexes: ["feliz", "contento" , "Alegre"],
                action: () => {
                    this._playAudio(require("../audios/mp3/RespuestaFeliz.mp3"));
                }
            },
            {
                indexes: ["triste"],
                action: () => {
                    this._playAudio(require("../videos/RespuestaTriste.mp4"));
                }
            },
            {
                indexes: ["salud"],
                action: () => {
                    this._playAudio(require("../videos/RespuestaSalud.mp4"));
                }
            },
            {
                indexes: ["empleo"],
                action: () => {
                    this._playAudio(require("../videos/RespuestaEmpleo.mp4"));
                }
            },
            {
                indexes: ["adios", "hasta luego", "chao"],
                action: () => {
                    Artyom.say("Hasta luego, ¡regresa pronto!",
                    {
                        /*onStart:function(){
                            _this.stopArtyom();
                        },*/
                        onEnd: function(){
                            _this.stopArtyom();
                        }
                    });
                }
            },

        ]);
    }
}