import React from "react";
import { withRouter, Link } from "react-router-dom";
import Scene from './Scene';

import Artyom from 'artyom.js';
const Jarvis = new Artyom();

class Confirmation extends React.Component{

    constructor (props, context){
        super(props, context);
  
        // Add `this` context to the handler functions    
        this.Continue = this.Continue.bind(this);

  
        // Prepare simple state
        this.state = {
            User: "",
            isDictating:false
        };
        // Load some commands to Artyom using the commands manager
        
    }

    texto = "hola";
    componentWillMount(){
        if(localStorage.getItem('emotion') == "tristeza" || localStorage.getItem('emotion') == "rabia" || localStorage.getItem('emotion') == "confusión" || localStorage.getItem('emotion') == "frustración"){
            this.texto = ", lamento que sientas "+ localStorage.getItem('emotion');
        }

        if(localStorage.getItem('emotion') == "agradecimiento" || localStorage.getItem('emotion') == "felicidad"){
            this.texto = ", me alegra que sientas "+ localStorage.getItem('emotion');
        }
    }

    //let User = "";
    //const history = useHistory(); 

    Continue(value) {
        value? this.props.history.push('/seleccion'): this.props.history.push('/hola')
    }

    
    //

    render(){
        return ( 
            <div className="container h-100 text-center " style={{display:"flex", flexDirection:"column", justifyContent:"space-between", zIndex:10}}>
                <h1 style={{fontSize:"4vw"}} className="font-weight-bold">
                    {localStorage.getItem('name') + this.texto } 
                    <small id="ayudacedula" className="form-text text-muted">Espero que después de nuestra conversación te sientas aún mejor.</small>
                </h1>
                
    
                <div style={{zIndex:10}}>
                    <p className="bg-primary badge ">¿Quieres contarme sobre qué tema específico quieres hablar?</p>
                    <form>
                        
                        <div className="form-group jd-flex flex-column ">
    
                            <div className="row jd-flex justify-content-center"> 

                                <Link className="btn btn-success mb-5 col-sm-3" to={{
                                    pathname:'/seleccion'}} style={{textDecoration:'none'}}>Si</Link>
                                <div className="col-sm-1"></div>
                                <Link className="btn btn-danger mb-5 col-sm-3" to={{
                                    pathname:'/'}} style={{textDecoration:'none'}}>No, me quiero ir</Link>  
                            </div>
                                         
                        </div>
                        
                    </form> 
                </div>
                <Scene></Scene>
            </div>      
        );
    }

    
};

export default withRouter(Confirmation)

