import React from "react";
import {Link, Redirect} from "react-router-dom";
import { withRouter } from "react-router-dom";
import Scene from './Scene';

import Artyom from 'artyom.js';
import { scenes } from "aframe";
const Jarvis = new Artyom();

let user = "";
function handleChangeInput1(e) {
    const inputValue = e.target.value;
    user=inputValue;
    console.log(user)
    //User = inputValue;
}

//const history = useHistory();
function handleSubmit1(evt) {
    //
    evt.preventDefault();
    //const history = useHistory();
    localStorage.setItem("name", user);
    this.props.history.push('/holi');
}

class Start extends React.Component{

    constructor (props, context){
        super(props, context);
  
        // Add `this` context to the handler functions
        this.StartAssistant = this.StartAssistant.bind(this);
        this.StopAssistant = this.StopAssistant.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleChangeInput = this.handleChangeInput.bind(this);
  
        // Prepare simple state
        this.state = {
            User: "",
            isDictating:false,
            buttonClass: "btn btn-danger"
        };
  
        // Load some commands to Artyom using the commands manager
        
    }

    //let User = "";
    //const history = useHistory(); 

    handleSubmit(evt) {
        //
        evt.preventDefault();
        this.StopAssistant();
        console.log(this.state.User);
        localStorage.setItem("name", document.getElementById("usuario").value);
        this.props.history.push('/emocion');
    }

    handleChangeInput(e) {
        const inputValue = e.target.value;
        this.setState({User:inputValue})
        console.log(this.state.User)
        //User = inputValue;
    }


    StopAssistant(){

        this.UserDictation.stop();
        let _this = this;
        Jarvis.fatality().then(() => {    
        }).catch((err) => {
            console.error("Oops, ha ocurrido un error!", err);
        });    
    }
    
    StartAssistant(){
        let _this = this;

        if(this.state.isDictating){
            this.UserDictation.stop();
            this.setState({
                isDictating:false,
                buttonClass:"btn btn-danger"
            });
        }
        else{
            this.UserDictation.start();
            this.setState({
                isDictating:true,
                buttonClass: "btn btn-warning"
            });
        }
        

        // UserDictation.stop();
    }

    UserDictation = Jarvis.newDictation({
        continuous:true, // Enable continuous if HTTPS connection
        onResult:function(text){
            // Do something with the text
            console.log(text);
            if(text!=""){document.getElementById("usuario").value = text;}
            
        },
        onStart:function(){
            console.log("Dictation started by the user");
        },
        onEnd:function(){
            console.log("Se ha desactivado el reconocimiento de voz, por favor escriba su nombre");
        }
    });
    
    //

    render(){
        let micro = ""
        if(!this.state.isDictating){
            micro = <img src={require("../images/micro2.webp")} height="20" alt="USA flag"/>
        }else{
            micro = <img src={require("../images/microred.png")} height="20" alt="USA flag"/>
        }
        return ( 
            <div className="container h-100 text-center" style={{display:"flex", flexDirection:"column", justifyContent:"space-between", zIndex:10}}>
                <h1 style={{fontSize:"4vw"}} className="font-weight-bold">Hola, te doy la bienvenida al Confesionario Digital, me alegra tu visita</h1>
    
                <div style={{zIndex:10}}>
                    <p className="bg-primary badge ">¿Me ayudas a recordarte?</p>
                    <form action="/instrucciones" onSubmit={this.handleSubmit}>
                        
                        <div className="form-group jd-flex flex-column ">
                            <label htmlFor="usuario" className="font-weight-bold bg-primary badge">Usuario:</label>
    
                            <div className="row jd-flex justify-content-center">                          
                                <div className="input-group col-sm-6 text-align:center ">
                                    <input type="text" className="form-control text-center" id="usuario" placeholder="Digita tu nombre o pronuncialo" name="usuario" onChange={this.handleChangeInput} required/>
                                    <span className="input-group-btn">
                                        <button className={this.state.buttonClass} type="button" onClick={this.StartAssistant}>
                                            {micro}
                                        </button>
                                    </span>
                                    <small id="ayuda" className="form-text text-muted w-100">Presiona sobre el botón rojo para activar el micrófono y pronuncia tu nombre</small>
                                </div>          
                            </div>
                                         
                        </div>
                        <button type="submit" className="btn btn-primary mb-5">  Guardar y Continuar  </button>
                    </form> 
                </div>
                <Scene></Scene>
            </div>      
        );
    }

    
};

export default withRouter(Start)

